import { NgModule } from '@angular/core';
import { UIRouterModule, RootModule, Ng2StateDeclaration } from 'ui-router-ng2';

import { HomeComponent, InformationsComponent } from './containers';

import { AppContainersModule } from './containers/app-containers.module';

// States

const homeState = {
  name: 'home',
  url: '/home',
  component: HomeComponent
};

const informacijeState = {
  name: 'informacije',
  url: '/informacije',
  component: InformationsComponent
};
const informationsState = {
  name: 'informations',
  url: '/informations',
  component: InformationsComponent
};

const dokumentiState = {
  name: 'dokumenti',
  url: '/dokumenti',
  component: HomeComponent
};
const documentsState = {
  name: 'documents',
  url: '/documents',
  component: HomeComponent
};

const dogadjajiState = {
  name: 'dogadaji',
  url: '/dogadjaji',
  component: HomeComponent
};
const eventsState = {
  name: 'events',
  url: '/events',
  component: HomeComponent
};

const kontaktState = {
  name: 'kontakt',
  url: '/kontakt',
  component: HomeComponent
};
const contactState = {
  name: 'contact',
  url: '/contact',
  component: HomeComponent
};


// END - States

const routes = {
  states: [
    homeState,
    informacijeState,
    informationsState,
    dokumentiState,
    documentsState,
    dogadjajiState,
    eventsState,
    kontaktState,
    contactState
  ],
  useHash: false,
  otherwise: {
    state: homeState.name
  }
};

@NgModule({
  imports: [
    AppContainersModule,
    UIRouterModule.forRoot(routes)
  ],
  exports: [UIRouterModule]
})
export class AppRoutingModule { }



